<template>
  <ul class="d-flex progress list-unstyled">
    <li class="progress-item" :class="{'active':step === 1}"></li>
    <li class="progress-item" :class="{'active':step === 2}"></li>
    <li class="progress-item" :class="{'active':step === 3}"></li>
  </ul>
</template>
<script>
export default {
  name: 'CheckoutProgress',
  props: {
    step: {
      type: Number
    }
  }
}
</script>
